import React from 'react'
import './About.scss';

import img1 from '../img/self.jpg';

const About = () => {
    return (
        <div className="About">
            <picture>
                <source media="(min-width: 461px)" srcSet={img1} />
                <img src={img1} className="About__self" alt="..." />
            </picture>
            <div className="About__txt">
                <p>Zapraszam Was do mojego świata. Świata powstałego z połączenia pasji- miłości do zwierząt, podróży i fotografii.</p>
                <p>Do przejrzenia się W moim Zwierciadle.</p>
                <p>Są relacje, miejsca i zdarzenia, które chcesz zachować na zawsze. Wiem, jak zrobić to dobrze.</p>
                <p>Wiem, jak wywołać uśmiech, a może i wzruszenie na Twojej twarzy przy odbiorze zdjęć z sesji.</p>
                <p>Podczas sesji pozostajesz w swojej strefie komfortu. Swobodnie i bez żadnej presji.</p>
                <p>Możesz też zdecydować o miejscu oraz stylu sesji- jeśli chcesz. Jeśli nie chcesz- zdaj się na mnie.</p>
            </div>
        </div>
    )
}

export default About
